.stepper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  position: relative;
  margin: 0.5rem 0;
}
.stepper::before {
  content: "";
  display: block;
  width: 100%;
  position: absolute;
  height: 1px;
  background-color: #898989;
  top: 50%;
  left: 0;
  z-index: 0;
}
.stepper.vertical {
  flex-flow: column nowrap;
}
.stepper .step {
  display: block;
  width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
  z-index: 1;
  background-color: #3a3a3a;
  color: #ffffff;
  cursor: pointer;
  position: relative;
}
.stepper .step span {
  display: block;
}
.stepper .step.complete {
  background-color: #008000;
  color: #ffffff;
}
.stepper .step.current {
  background-color: #94b6ff;
  color: #ffffff;
}
.stepper.cycle .step {
  border-radius: 50%;
}
.stepper.diamond .step {
  transform: rotate(45deg);
}
.stepper.diamond .step span {
  transform: rotate(-45deg);
}
