.keypad {
  background-color: #ffffff;
  color: #1d1d1d;
  border: 1px solid #d9d9d9;
}
.keypad input {
  border: 0;
}
.keypad .keys {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: auto;
  float: left;
  background-color: inherit;
  color: inherit;
  border: none;
  padding: 0;
  user-select: none;
  transition: all 0.3s ease-in-out;
  z-index: 1000;
}
.keypad .keys::before {
  content: '';
  position: absolute;
  background-color: inherit;
  width: 10px;
  height: 10px;
  border: 1px solid var(--border-color);
  z-index: 2;
  left: -5px;
  top: 50%;
  transform: translateY(-50%);
  margin-top: -5px;
  border-top-color: transparent;
  border-right-color: transparent;
  transform: rotate(45deg);
}
.keypad .keys.open {
  display: block;
  transition: all 0.3s ease-in-out;
}
.keypad .keys.right {
  left: 100%;
  transform: translateY(-50%);
  top: 50%;
  margin-left: -1px;
}
.keypad .keys.bottom {
  left: 50%;
  transform: translateX(-50%);
  top: 100%;
  margin-top: -1px;
}
.keypad .keys.bottom::before {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transform: rotate(135deg);
}
.keypad .keys.top {
  top: 0;
  left: 50%;
  transform: translateY(-100%) translateX(-50%);
  margin-top: 1px;
}
.keypad .keys.top::before {
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  transform: rotate(-45deg);
}
.keypad .keys.left {
  left: 0;
  transform: translateY(-50%) translateX(-100%);
  top: 50%;
  margin-left: 1px;
}
.keypad .keys.left::before {
  top: 50%;
  transform: translateY(-50%);
  left: 100%;
  margin-left: -5px;
  transform: rotate(225deg);
}
.keypad .keys.bottom-left {
  top: 100%;
  left: 0;
  margin-top: -1px;
}
.keypad .keys.bottom-left::before {
  top: 0;
  left: 0.625rem;
  transform: rotate(135deg);
}
.keypad .keys.bottom-right {
  top: 100%;
  left: 100%;
  transform: translateX(-100%);
  margin-top: -1px;
}
.keypad .keys.bottom-right::before {
  top: 0;
  left: 100%;
  margin-left: -1rem;
  transform: rotate(135deg);
}
.keypad .keys.top-left {
  top: 0;
  left: 0;
  transform: translateY(-100%);
  margin-top: 1px;
}
.keypad .keys.top-left::before {
  top: 100%;
  left: 0.625rem;
  transform: rotate(-45deg);
}
.keypad .keys.top-right {
  top: 0;
  left: 100%;
  transform: translateY(-100%) translateX(-100%);
  margin-top: 1px;
}
.keypad .keys.top-right::before {
  top: 100%;
  left: 100%;
  margin-left: -1rem;
  transform: rotate(-45deg);
}
.keypad .key {
  display: block;
  position: relative;
  float: left;
  background-color: inherit;
  color: inherit;
  margin: 0;
  text-align: center;
  border: 1px solid var(--border-color);
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.keypad .key:hover {
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 0 3px rgba(244, 244, 244, 0.45);
  transform: scale(1.2);
  z-index: 2;
}
.keypad .key.service-key {
  background-color: #f8f8f8;
}
