.tag {
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  margin: 2px;
  padding: 0;
  height: 26px;
  line-height: 26px;
  background-color: #f8f8f8;
  cursor: default;
}
.tag .title {
  display: block;
  position: relative;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0 8px;
}
.tag .remover {
  display: block;
  position: relative;
  width: 26px;
  height: 100%;
  line-height: 22px;
  text-align: center;
  cursor: pointer;
  background-color: #f4f4f4;
  font-size: 1.2rem;
}
.tag.short-tag .title {
  max-width: 120px;
}
.tag.small {
  margin: 2px 2px;
  height: 18px;
  line-height: 18px;
}
.tag.small .title {
  font-size: 0.7rem;
  line-height: 18px;
}
.tag.small .remover {
  width: 18px;
  height: 18px;
  line-height: 18px;
  font-size: 0.8rem;
}
.tag.large {
  margin: 2px 2px;
  height: 36px;
  line-height: 36px;
}
.tag.large .title {
  font-size: 1.3rem;
  line-height: 36px;
}
.tag.large .remover {
  width: 35px;
  height: 35px;
  line-height: 33px;
  font-size: 1.6rem;
}
.tag.static {
  background: #d4f0ff;
  color: #1d1d1d;
}
.tag.static .remover {
  display: none!important;
}
