.chat {
  display: flex;
  flex-flow: column;
  border: 1px solid var(--border-color);
  background-color: #ffffff;
}
.chat .title,
.chat .messages,
.chat .message-input {
  display: block;
}
.chat .title {
  font-size: 18px;
  line-height: 1.2;
  padding: 8px;
  border-bottom: 1px solid var(--border-color);
}
.chat .messages {
  padding: 8px;
  border-bottom: 1px solid var(--border-color);
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
}
.chat .message {
  display: flex;
  flex-flow: row wrap;
}
.chat .message .message-sender {
  font-weight: bold;
  font-size: 12px;
  order: 1;
  position: absolute;
  top: 6px;
  left: 6px;
}
.chat .message .message-time {
  font-size: 11px;
  order: 2;
  opacity: 0.5;
  position: absolute;
  left: 6px;
  bottom: 6px;
}
.chat .message .message-item {
  width: 100%;
  flex-shrink: 0;
  display: flex;
  flex-flow: row nowrap;
  margin: 2px 0 0;
  order: 3;
}
.chat .message .message-item .message-avatar {
  overflow: hidden;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  flex-shrink: 0;
  display: block;
  order: 1;
}
.chat .message .message-item .message-text {
  display: flex;
  align-items: center;
  border: 1px solid #f4f4f4;
  margin-left: 8px;
  order: 2;
  font-size: 14px;
  line-height: 1.2;
  padding: 30px 10px;
  min-width: 100px;
  max-width: 100%;
  background-color: #f4f4f4;
  position: relative;
}
.chat .message .message-item .message-text-inner {
  width: 100%;
  overflow: hidden;
}
.chat .message .message-item .message-text::before {
  position: absolute;
  right: 100%;
  top: 14px;
  border: 6px solid transparent;
  border-right-color: inherit;
  content: ' ';
  height: 0;
  width: 0;
  pointer-events: none;
}
.chat .message .message-item .message-text.--next {
  padding-top: 10px;
}
.chat .message .message-item .message-text.--next::before {
  display: none;
}
.chat .message.right .message-item {
  justify-content: flex-end;
}
.chat .message.right .message-time {
  order: 1;
  text-align: left;
  left: auto;
  right: 6px;
}
.chat .message.right .message-sender {
  order: 2;
  left: auto;
  right: 6px;
}
.chat .message.right .message-avatar {
  order: 2;
}
.chat .message.right .message-text {
  order: 1;
  margin-left: 0;
  margin-right: 8px;
}
.chat .message.right .message-text::before {
  right: auto;
  left: 100%;
  border-right-color: transparent;
  border-left-color: inherit;
}
.chat .message.right .message-text.--next::before {
  display: none;
}
.chat .message-input {
  flex-shrink: 0;
  margin-top: auto;
  padding: 8px;
}
.chat .message-input.disabled {
  opacity: 0.5;
}
.chat .message.left + .message.right {
  margin-top: 10px;
}
.chat .message.right + .message.left {
  margin-top: 10px;
}
