.wheel-picker {
  display: block;
  position: relative;
  width: 100%;
  min-width: 220px;
  cursor: default;
  background-color: #ffffff;
  color: #1d1d1d;
  height: 38px;
  user-select: none;
}
.wheel-picker .select-wrapper {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  height: auto;
  z-index: 3;
  width: 100%;
}
.wheel-picker .select-block {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  flex-shrink: 0;
  background-color: #f2f2f2;
}
.wheel-picker .select-block::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 0;
  height: 40px;
  background-color: rgba(148, 182, 255, 0.5);
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 100%;
}
.wheel-picker .select-block ul {
  margin: 0;
  padding: 0;
  list-style: none inside;
  display: block;
  text-align: center;
  height: 100%;
  z-index: 1;
  flex: 1;
  border: 1px solid var(--border-color);
  overflow-y: auto;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  overflow: -moz-scrollbars-none;
}
.wheel-picker .select-block ul::-webkit-scrollbar {
  display: none!important;
}
.wheel-picker .select-block ul li {
  display: block;
  height: 40px;
  line-height: 40px;
  font-size: 0.9em;
}
.wheel-picker .select-block ul li.active {
  font-weight: bold;
}
.wheel-picker .action-block {
  display: flex;
  flex-flow: row nowrap;
  position: relative;
  width: 100%;
  background-color: #ffffff;
}
.wheel-picker .action-block > * {
  flex: 1;
}
.wheel-picker .action-block > *:not(:last-child) {
  border-right: 1px solid var(--border-color);
}
.wheel-picker .select-wrapper {
  transform: translate3d(0, -50%, 0);
}
.wheel-picker.for-top .select-wrapper,
.wheel-picker.for-top-a .select-wrapper {
  transform: translate3d(0, 0, 0);
}
.wheel-picker.for-bottom .select-wrapper,
.wheel-picker.for-bottom-a .select-wrapper {
  transform: translate3d(0, -100%, 0);
}
