:root {
  --remark-border-color: var(--border-color);
  --remark-background: #ffffff;
  --remark-color: #191919;
}
.dark-side {
  --remark-border-color: var(--border-color);
  --remark-background: #26282e;
  --remark-color: #dbdfe7;
}
.remark {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: relative;
  border: 1px solid;
  border-left: 4px solid;
  border-color: var(--remark-border-color);
  color: var(--remark-color);
  background-color: var(--remark-background);
  padding: 1rem;
  font-size: 14px;
  margin: 1rem 0;
  cursor: default;
  overflow: hidden;
}
.remark.rounded {
  border-radius: 10px!important;
}
.remark .icon {
  margin-right: 1rem;
  font-size: 32px;
  width: 32px;
  height: 32px;
}
.remark.primary {
  border: 1px solid #5ca7fd !important;
  color: #191919 !important;
}
.remark.primary::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  content: " ";
  background-color: #0366d6;
  z-index: 0;
}
.remark.primary .icon {
  color: #0366d6 !important;
}
.remark.primary {
  background: #daebfe !important;
}
