.streamer {
  position: relative;
  display: block;
  width: 100%;
  overflow: hidden;
  margin-bottom: 1rem;
}
.streamer .streams {
  width: 40px;
  overflow: visible;
  display: flex;
  flex-flow: column nowrap;
  padding-top: 30px;
  background-color: #ffffff;
  color: #1d1d1d;
  position: absolute;
  margin-top: 2px;
  left: 0;
  top: 0;
  z-index: 500;
}
.streamer .streamer-actions {
  position: absolute;
  top: -1px;
  left: 0;
  width: 100%;
  height: 32px;
  display: none;
  flex-flow: row nowrap;
  justify-content: flex-start;
  background-color: inherit;
  z-index: 101;
}
.streamer .streamer-action {
  margin: 1px;
  height: 26px;
  width: 26px;
  border: 0 solid var(--border-color);
  line-height: 26px;
  text-align: center;
  cursor: pointer;
  background-color: #ffffff;
  color: #1d1d1d;
  outline: none;
  padding: 0;
}
.streamer .streamer-action * {
  vertical-align: inherit ;
  font-size: 24px;
  text-align: center;
  width: 24px;
  height: 24px;
}
.streamer a.streamer-action {
  color: #1d1d1d;
}
.streamer a.streamer-action:hover {
  color: #1d1d1d;
}
.streamer .stream {
  position: relative;
  height: 75px;
  width: 40px;
  padding: 4px 8px;
  display: flex;
  flex-flow: column nowrap;
  background-color: inherit;
  color: inherit;
  border: 2px solid #ffffff;
  cursor: pointer;
  user-select: none;
  transition: width 0.3s ease;
}
.streamer .stream-title,
.streamer .stream-secondary {
  display: none;
  z-index: 2;
}
.streamer .stream-title {
  font-size: 14px;
  text-transform: uppercase;
  height: 100%;
  line-height: 1;
}
.streamer .stream-secondary {
  height: 16px;
  line-height: 16px;
  font-size: 12px;
}
.streamer .stream-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -16px;
  margin-left: -16px;
  width: 32px;
  height: 32px;
  font-size: 24px;
  text-align: center;
}
.streamer .events-area {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-flow: column nowrap;
  padding-left: 40px;
  z-index: 1;
  position: relative;
  white-space: nowrap;
  overflow-x: scroll;
  -webkit-overflow-scrolling: auto;
}
.streamer .streamer-timeline,
.streamer .streamer-fake-timeline {
  height: 32px;
  width: auto;
  list-style: none inside;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: row nowrap;
  order: 1;
}
.streamer .streamer-timeline li,
.streamer .streamer-fake-timeline li {
  flex-shrink: 0;
  flex-grow: 0;
  width: 224px;
  background: url("data:image/png;base64,") no-repeat;
  vertical-align: bottom;
  height: 100%;
  line-height: 30px;
  display: flex;
  flex-flow: row nowrap;
  position: relative;
}
.streamer .streamer-timeline li em,
.streamer .streamer-fake-timeline li em {
  font-size: 10px;
  font-style: normal;
  margin: 4px;
  flex: 1 0 0;
  max-width: 100%;
}
.streamer .streamer-fake-timeline {
  position: absolute;
  height: 1px;
  top: 0;
  left: 0;
  width: 100%;
}
.streamer .streamer-fake-timeline li {
  width: auto;
  line-height: 1px;
  flex-shrink: 0;
  flex-grow: 0;
  overflow: hidden;
  text-align: center;
  color: #fcfcfc;
}
.streamer .streamer-events {
  order: 2;
  position: relative;
  display: flex;
  flex-flow: row nowrap;
}
.streamer .event-group {
  display: block;
  background-color: #ffffff;
  color: #1d1d1d;
  border: 1px solid transparent;
  flex-shrink: 0;
  margin-top: -1px;
}
.streamer .stream-events {
  min-height: 75px;
  height: auto;
  width: auto;
  position: relative;
  display: flex;
  flex-flow: row nowrap;
}
.streamer .stream-events.global-stream {
  min-height: 100%;
}
.streamer .stream-events.global-stream .stream-event {
  min-width: 100%;
}
.streamer .stream-event {
  width: 222px;
  height: 72px;
  border: 1px solid #f5f5f5;
  margin: 2px 1px;
  background-color: #ffffff;
  color: #1d1d1d;
  overflow: hidden;
  user-select: none;
  cursor: pointer;
}
.streamer .stream-event:hover {
  border-color: var(--border-color);
}
.streamer .stream-event-slide {
  display: flex;
  position: relative;
  flex-flow: row nowrap;
  height: 100%;
}
.streamer .stream-event-slide .slide-logo {
  width: 40px;
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  padding: 2px;
}
.streamer .stream-event-slide .slide-logo .icon {
  width: 36px;
  height: 36px;
}
.streamer .stream-event-slide .slide-logo .time {
  margin-top: 1px;
  padding: 5px 4px 6px;
  font-size: 0.75rem;
}
.streamer .stream-event-slide .slide-data {
  margin: 0 4px;
  padding: 4px 0;
  display: flex;
  flex-flow: column nowrap;
  width: auto;
  max-width: calc(100% - 48px);
  height: 100%;
}
.streamer .stream-event-slide .slide-data .title,
.streamer .stream-event-slide .slide-data .subtitle,
.streamer .stream-event-slide .slide-data .desc {
  line-height: 1;
}
.streamer .stream-event-slide .slide-data .title {
  font-size: 12px;
  font-weight: bold;
  line-height: 14px;
}
.streamer .stream-event-slide .slide-data .subtitle {
  font-size: 10px;
}
.streamer .stream-event-slide .slide-data .desc {
  font-size: 11px;
  color: #999999;
  margin-top: auto;
  height: 29px;
}
.streamer .stream-event-slide .slide-data * + .subtitle {
  margin-top: 2px;
}
.streamer .stream-event-slide .state-icon {
  position: absolute;
  z-index: 99;
  top: 4px;
  right: 8px;
  color: #3a3a3a;
}
.streamer .stream-event {
  position: relative;
  user-select: none;
}
.streamer .stream-event.selected {
  box-shadow: 0 0 0 1px #f4fbff !important;
}
.streamer .stream-events:not(.global-stream) .stream-event.disabled {
  opacity: 0.2;
}
