:root {
  --button-background: #ebebeb;
  --button-background-hover: #dadada;
  --button-color: #191919;
  --button-border-color: #ebebeb;
  --button-border-radius: 4px;
  --button-pill-radius: 18px;
  --button-size: 36px;
}
.dark-side {
  --button-background: #2b2d30;
  --button-background-hover: #333439;
  --button-color: #f3fcff;
  --button-border-color: #4e5157;
}
.button,
.flat-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  padding: 0 1rem;
  font-size: 14px;
  height: var(--button-size);
  transition: all 0.15s ease-in-out;
  background-color: var(--button-background);
  color: var(--button-color);
  border: 1px solid var(--button-border-color);
  cursor: pointer;
  outline: none;
  position: relative;
  overflow: visible;
  border-radius: var(--button-border-radius);
}
.button:first-child,
.flat-button:first-child {
  margin-left: 0;
}
.button:last-child,
.flat-button:last-child {
  margin-right: 0;
}
.button * + *,
.flat-button * + * {
  margin-left: 8px;
}
.button.disabled,
.flat-button.disabled,
.button:disabled,
.flat-button:disabled {
  pointer-events: none;
  opacity: 0.65;
}
.button .icon,
.flat-button .icon {
  height: calc(var(--button-size)/2);
  font-size: calc(var(--button-size)/2);
}
.button.pill-button,
.flat-button.pill-button {
  border-radius: var(--button-pill-radius);
}
.button:active,
.button.focus,
.button:focus {
  text-decoration: none;
}
.button:hover {
  background-color: var(--button-background-hover);
}
.button.link {
  background-color: transparent;
  border: none;
}
.button.link:hover {
  background-color: transparent;
  text-decoration: underline;
}
.button.link.focus,
.button.link:focus {
  box-shadow: none;
}
.button.mini {
  --button-pill-radius: 10px;
  --button-size: 20px;
  font-size: 10px;
  padding: 0 0.5rem;
}
.button.small {
  --button-pill-radius: 13px;
  --button-size: 26px;
  font-size: 12px;
  padding: 0 0.6rem;
}
.button.large {
  --button-pill-radius: 27px;
  --button-size: 54px;
  font-size: 18px;
  padding: 0 1rem;
}
.button.square,
.button.cycle {
  width: 36px;
  height: 36px;
  padding: 0!important;
  flex-shrink: 0!important;
  flex-grow: 0!important;
}
.button.square.mini,
.button.cycle.mini {
  width: 20px;
  height: 20px;
}
.button.square.small,
.button.cycle.small {
  width: 26px;
  height: 26px;
}
.button.square.large,
.button.cycle.large {
  width: 54px;
  height: 54px;
}
.button.cycle {
  border-radius: 50%;
}
.button.square.dropdown-toggle::before,
.button.cycle.dropdown-toggle::before {
  display: none;
}
.button.dropdown-toggle.no-marker {
  padding-right: 0.75rem !important;
}
.button.outline {
  background: transparent none;
  border-width: 1px;
  border-color: var(--button-border-color);
}
a.button,
a.flat-button {
  appearance: none;
  color: var(--button-color);
  box-shadow: none;
  text-decoration: none;
}
