.time-picker input {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.time-picker input:focus ~ .time-wrapper {
  box-shadow: 0 0 0 3px rgba(244, 244, 244, 0.45);
}
.time-picker .time-wrapper {
  display: inline-flex;
  flex-flow: row nowrap;
  position: relative;
  border: 1px solid var(--border-color);
  width: 100%;
}
.time-picker .hours,
.time-picker .minutes,
.time-picker .seconds {
  display: block;
  flex: 1;
  border: 1px solid;
  border-color: inherit;
  text-align: center;
  height: 38px;
  line-height: 34px;
  position: relative;
  font-size: 1.2em;
}
.time-picker.show-labels .hours,
.time-picker.show-labels .minutes,
.time-picker.show-labels .seconds {
  line-height: 30px;
}
.time-picker.show-labels .hours::before,
.time-picker.show-labels .minutes::before,
.time-picker.show-labels .seconds::before {
  content: attr(data-title);
  position: absolute;
  display: block;
  font-size: 8px;
  line-height: 12px;
  left: 50%;
  bottom: 0;
  transform: translate3d(-50%, 0, 0);
}
.time-picker.disabled {
  pointer-events: none!important;
  color: #f4f4f4 !important;
}
