:root {
  --info-box-border-radius: 6px;
  --info-box-background: var(--default-background);
  --info-box-color: var(--default-color);
  --info-box-border-color: var(--border-color);
}
.dark-side {
  --info-box-background: var(--default-background);
  --info-box-color: var(--default-color);
  --info-box-border-color: var(--border-color);
}
.info-box {
  position: fixed;
  background-color: var(--info-box-background);
  color: var(--info-box-color);
  border: 1px solid var(--info-box-border-color);
  display: block;
  z-index: 1050;
  max-width: calc(100vw - 100px);
  max-height: calc(100vh - 100px);
}
.info-box .closer {
  display: block;
  position: absolute;
  top: 4px;
  right: 4px;
  background-color: inherit;
  color: inherit;
  z-index: 100;
}
.info-box .closer::before {
  display: block;
  content: "\00d7";
  font-size: 24px;
  margin-top: -4px;
}
.info-box .info-box-content {
  padding: 20px;
  z-index: 0;
}
.info-box2 {
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: var(--info-box-border-radius);
  border: 1px solid var(--info-box-border-color);
  background: var(--info-box-background);
  color: var(--info-box-color);
  padding: 4px 8px;
}
.info-box2 .title {
  text-transform: uppercase;
  font-size: 9px;
  font-weight: 400;
}
.info-box2 .value {
  display: flex;
  font-size: 16px;
  font-weight: 500;
  align-items: flex-end;
  justify-content: space-between;
}
.info-box2 .value .primary-value {
  font-size: 1em;
}
.info-box2 .value .secondary-value {
  font-size: 0.8em;
  margin-left: auto;
}
