@property --border-spin-angle {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}
:root {
  --animated-border-speed: 3s;
  --animated-border-color-1: #ff4545;
  --animated-border-color-2: #00ff99;
  --animated-border-color-3: #006aff;
  --animated-border-color-4: #ff0095;
  --animated-border-color-5: #ff4545;
}
.animated-border,
[class*=animated-border-] {
  position: relative;
}
.animated-border::after,
[class*=animated-border-]::after,
.animated-border::before,
[class*=animated-border-]::before {
  content: '';
  position: absolute;
  background-image: conic-gradient(from var(--border-spin-angle), var(--animated-border-color-1), var(--animated-border-color-2), var(--animated-border-color-3), var(--animated-border-color-4), var(--animated-border-color-5));
  z-index: -1;
  padding: 3px;
  border-radius: inherit;
  inset: -2px;
  animation: var(--animated-border-speed) spin-border linear infinite;
}
.animated-border::before,
[class*=animated-border-]::before {
  filter: blur(0.5rem);
  opacity: 0.5;
}
.animated-border-hover::after,
.animated-border-hover::before {
  background-image: initial;
}
.animated-border-hover:hover::after,
.animated-border-hover:hover::before {
  background-image: conic-gradient(from var(--border-spin-angle), #ff4545, #00ff99, #006aff, #ff0095, #ff4545);
}
@keyframes spin-border {
  from {
    --border-spin-angle: 0deg;
  }
  to {
    --border-spin-angle: 360deg;
  }
}
