.window {
  display: flex;
  flex-direction: column;
  position: relative;
  height: auto;
  background-color: #ffffff;
  color: #1d1d1d;
  border: 1px #e9e9e9 solid;
  z-index: 1;
}
.window-caption {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  height: 2.25rem;
  line-height: 2.25rem;
  padding: 0;
  border-bottom: 1px transparent solid;
  cursor: default;
  vertical-align: middle;
  background-color: #3c6478;
  color: #ffffff;
  user-select: none;
}
.window-caption .title {
  font-size: 0.875rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
  padding-left: 4px;
  color: inherit;
}
.window-caption .icon {
  width: 34px;
  height: 34px;
  text-align: center;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: inherit;
}
.window-caption .icon * {
  width: 1rem;
  font-size: 1rem;
  line-height: 34px;
  text-align: center;
}
.window-caption * + .title {
  padding-left: 0;
}
.window-caption .buttons {
  height: 100%;
  margin-left: auto;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}
.window-caption .btn-custom:focus,
.window-caption .btn-custom:active {
  box-shadow: none!important;
}
.window-caption .buttons .btn-min,
.window-caption .buttons .btn-max,
.window-caption .buttons .btn-close,
.window-caption .buttons .btn-custom {
  position: relative;
  height: 34px;
  width: 34px;
  text-align: center;
  vertical-align: middle;
  font-size: 1rem;
  font-weight: normal;
  padding: 0 0 0.625rem 0;
  outline: none;
  cursor: pointer;
  display: block;
  border: 1px solid transparent;
  margin: 1px 0;
}
.window-caption .buttons .sys-button {
  background-color: transparent;
  color: #ffffff;
}
.window-caption .buttons .sys-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.window-caption .buttons .sys-button:hover::before {
  border-color: #ffffff;
  color: #ffffff;
}
.window-caption .buttons .sys-button:active {
  background-color: rgba(255, 255, 255, 0.3);
  color: #ffffff;
}
.window-caption .buttons .btn-close:hover {
  background-color: #c75050;
  color: #ffffff;
}
.window-caption .buttons .btn-close:hover::before {
  color: #ffffff;
}
.window-caption .buttons .btn-close:active {
  background-color: #e04343;
  color: #ffffff;
}
.window-caption .buttons .btn-close::before {
  border-color: #777777;
  content: '\D7';
  position: absolute;
  line-height: 34px;
  vertical-align: middle;
  left: 50%;
  top: -4px;
  margin-left: -0.32em;
  color: #fff;
  font-size: 1.5rem;
}
.window-caption .buttons .btn-min::before,
.window-caption .buttons .btn-max::before {
  display: block;
  position: absolute;
  width: 0.625rem;
  height: 0.625rem;
  border: 0 #fff solid;
  border-bottom-width: 2px;
  content: ' ';
  bottom: 0.72rem;
  left: 50%;
  margin-left: -0.3rem;
  top: auto;
}
.window-caption .buttons .btn-max::before {
  height: 0.575rem;
  border: 1px #fff solid;
  border-top-width: 2px;
}
.window-caption .buttons .btn-custom {
  min-width: 34px;
  width: auto;
  flex-shrink: 0;
}
.window-caption .buttons .btn-custom {
  order: 1;
}
.window-caption .buttons .btn-min {
  order: 2;
}
.window-caption .buttons .btn-max {
  order: 3;
}
.window-caption .buttons .btn-close {
  order: 4;
}
.window-content {
  overflow: auto;
  height: calc(100% - 2.25rem);
}
.window-status {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 0.1rem 0.5rem;
  background-color: #f8f8f8;
  color: #1d1d1d;
  height: 1.2rem;
  font-size: 0.8rem;
}
.window.info .window-caption {
  background-color: #94b6ff;
  color: #ffffff;
}
.window.alert .window-caption {
  background-color: #ff0000;
  color: #ffffff;
}
.window.warning .window-caption {
  background-color: #ffa500;
  color: #ffffff;
}
.window.success .window-caption {
  background-color: #008000;
  color: #ffffff;
}
.window.focused {
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.3);
}
.window.modal {
  z-index: 1050;
}
.window.minimized {
  max-width: 12rem;
  max-height: 2.25rem;
}
.window.minimized .window-caption {
  border-bottom: none!important;
}
.window.minimized .window-content {
  display: none!important;
}
.window.minimized .window-caption .btn-custom {
  display: none;
}
.window.maximized {
  top: 0!important;
  left: 0!important;
  width: 100% !important;
  height: 100% !important;
  max-height: none !important;
  max-width: none !important;
}
.window.maximized .btn-max::before {
  border: 1px #fff solid;
  z-index: 2;
}
.window.maximized .btn-max::after {
  display: block;
  position: absolute;
  width: 0.625rem;
  height: 0.625rem;
  border-top: 1px #fff solid;
  border-right: 1px #fff solid;
  content: ' ';
  bottom: 0.375rem;
  right: 7px;
  top: 8px;
  z-index: 1;
}
.window.resizable {
  min-width: 12rem;
  min-height: 2.25rem;
}
